body {
  font-family: sans-serif;
}

#app {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.calculator {
  margin: 10px;
  width: 100%;
  max-width: 800px;
  height: 530px;
  background-color: gainsboro;
  border: 5px solid black;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  flex-wrap: nowrap;
}
.calculator-name {
  width: 95%;
  height: 70px;
  background-color: black;
  border-radius: 10px;
  color: azure;
  text-align: center;
  line-height: 100%;
  margin: 10px;
}
#display-container {
  width: 95%;
  height: 80px;
  border: 1px solid black;
  background-color: white;
  border-radius: 10px;
  margin: 10px;
}
#display {
  text-align: end;
  margin: 10px;
  font-size: 30px;
  font-weight: bold;
}
.keypad {
  width: 95%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  flex-wrap: nowrap;
  margin: 10px;
}
.row {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-around;
  margin-top: 2px;
}
.button {
  width: 24%;
  max-width: 192px;
  height: 50px;
  background-color: white;
  border: 2px solid black;
  border-radius: 4px;
  text-align: center;
  line-height: 50px;
  margin-left: 1px;
  margin-right: 1px;

  font-weight: bold;
  font-size: 25px;
}
.zero {
  width: 49%;
  max-width: 392px;
}
.last {
  background-color: firebrick;
  color: white;
}
.button:hover {
  background-color: burlywood;
}
.last:hover {
  background-color: red;
}
